@media screen and (max-width: 640px) {

    /*home page*/
    .container-home {
        margin-bottom: 3rem;
    }

    .container-home .card {
        display: none;
    }

    .container-home .home-main {
        margin: 0;
        width: initial;
    }

    .container-home .main-head {
        margin: 0;
        width: initial;
    }

    .container-home .card-area {
        flex-direction: column;
        margin-left: auto;
        margin-right: auto;
    }

    .container-home .smallcards {
        margin: 1rem auto;
    }

    .container-home .main {
        width: 100%;
    }

    .container-home .form-card-second {
        width: initial;
        margin: 1rem 1.5rem;
        padding: 0.5rem 1rem;
    }

    .form-card-second label {
        width: auto;
        text-align: left;
    }

    /*registration*/
    .top-label-section {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0.5rem;
    }

    .top-label-section .dropdown {
        width: 100%;
        margin: 0;
    }

    .top-label-section input[type="text"],
    input[type="password"],
    [type="date"],
    .top-label-section button {
        width: 100%;
        margin: 0;
    }

    .dropdown-school-section {
        margin-top: 0.5rem;
        margin-bottom: 2.5rem;
    }

    .dropdown-school-section .dropdown-school {
        width: 100%;
        margin: 0;
    }

    /*login page*/
    .container-login {
        margin-left: 1.5rem;
        margin-right: 1.5rem;
        margin-bottom: 3rem;
    }

    .container-login .container-inner-area {
        flex-direction: column;
        margin-top: 1.5rem;
    }

    .container-login .smallcards-login {
        margin: 1rem auto;
    }

    .container-login .login-card {
        width: auto;
        padding: 0.5rem 1rem
    }

    .login-card label {
        text-align: left;
        width: auto;
    }

    /*generic*/
    .imgcontainer {
        margin: auto;
        padding-bottom: 1rem;
    }

    .form-card {
        width: initial;
        padding: 0 1rem;
    }

    .input-wrap {}

    .input-wrap input[type="otp"] {}

    .input-wrap input[type="text"],
    input[type="password"],
    [type="date"] {
        margin-left: 0;
        margin-right: 0;
    }

    .form-card label,
    .form-label {
        width: initial;
    }

    .otpbutton,
    .dropdown,
    input[type="text"],
    input[type="password"],
    [type="date"] {
        padding-left: 1rem;
        padding-right: 1rem;
    }

    .input-wrap .otpbutton {
        width: initial;
        margin: 4px;
    }

    .otpbutton {
        margin-top: 1rem;
    }

    .main-btn {
        width: 100%;
    }

    /*footer*/
    .fixed-footer {
        position: static;
    }

    footer {
        position: relative;
        width: 100%;
    }
}